.landing {
  &-body {
    background: url('data:image/png;base64,');

    .footer-fix {
      height: 880px;
    }

    h1 {
      text-align: center;
      padding-bottom: 80px;
    }
  }

  &-main {
    position: absolute;
    width: 1000px;
    height: 420px;
    top: 50%;
    left: 50%;
    margin: -320px 0 0 -500px;
  }

  &-col {
    width: 350px;

    padding: 0 20px;

    .list-main {
      min-height: 110px;
    }

    &.push-left {
      padding-left: 75px;
    }
  }
}