.address {

  &-left {
    width: 470px;
    float: left;
    padding: 0 30px 0 0;
  }

  &-right {
    float: left;
    width: 500px;
    overflow: hidden;
    border-radius: $border-radius-main + px;
  }

  &-list {
    margin: 0;
    padding: 0;
    list-style: none;

    &-item {
      padding: 0 0 10px 15px;
      position: relative;

      &:after {
        content: '';
        display: block;
        clear: both;
      }

      &-info {
        float: right;
        color: $text-grey;
        text-align: right;
        width: 175px;
      }

      &-more {
        color: $text-grey;
      }

      &__main {
        padding-bottom: 35px;
      }
    }

    &-metro {
      &:before {
        content: '';
        width: 7px;
        height: 7px;
        border-radius: 100%;
        position: absolute;
        top: 6px;
        left: 0;
      }

      &__orange {
        &:before {
          background: #de823a;
        }
      }
      &__red {
        &:before {
          background: #9e2a47;
        }
      }
      &__green {
        &:before {
          background: #35a45b;
        }
      }
      &__grey {
        &:before {
          background: #c4c4c4;
        }
      }
      &__greenlight {
        &:before {
          background: #c0dc92;
        }
      }
      &__blue {
        &:before {
          background: #56bdef;
        }
      }
      &__violet {
        &:before {
          background: #293694;
        }
      }
    }
  }

  &-more {
    margin: 0;
    padding: 0;
    list-style: none;

    &-item {
      padding: 0 0 0 0;
      margin: 0 0 10px 0;

      &:after {
        content: '';
        display: block;
        clear: both;
        height: 1px;
        position: relative;
        top: -4px;
        background: url('data:image/false;base64,') repeat-x
      }

      &-left {
        position: relative;
        z-index: 1;
        float: left;
        width: 50%;

        span {
          background: #fff;
          padding: 0 7px 0 0;
        }
      }

      &-right {
        float: right;
        text-align: right;
        position: relative;
        z-index: 1;
        width: 50%;

        span {
          background: #fff;
          padding: 0 0 0 7px;
        }
      }
    }
  }

  &-messages {
    font-family: 'helveticaneuecyrbold';
    color: $text-red;
    line-height: 24px;
    padding: 45px 0 0 0;
  }
}