  line-height: 1px;
}

.push-right {
  float: right;
}

.push-left {
  float: left;
}

/* new */
.fullscreen-loading {
  position: fixed;
  height: 100%;
  width: 100%;
  background: white;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: .9;
  font-family: "helveticaneuecyrmedium";
  color: black;
  left: 0;
  top: 0;
}

.loading:after {
  overflow: hidden;
  display: inline-block;
  vertical-align: bottom;
  -webkit-animation: ellipsis steps(4, end) 900ms infinite;
  animation: ellipsis steps(4, end) 900ms infinite;
  /* content: "..."; /* ascii code for the ellipsis character */
  content: "\2026";
  /* ascii code for the ellipsis character */
  width: 0px;
  position: absolute;
}

.loading-inside-button {
  padding: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

/*
 * Hide visually and from screen readers, but maintain layout
 */
.invisible {
  visibility: hidden;
}

@keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

@-webkit-keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

.pointer {
  cursor: pointer !important;
}

a {
  cursor: pointer;
}

button {
  outline: none;
}

