body {
  -webkit-overflow-scrolling: touch;
}

body.modal-open {
  overflow-x: scroll;
  overflow-y: hidden;
}

body.modal-open:before {
  content: '';
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  position: fixed;
  z-index: 99;
}

.lightbox-background {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  display: none;
}

.modal {
  position: fixed;
  width: 60%;
  height: 50%;
  background: white;
  z-index: 99;
  left: 20%;
  top: 25%;
  border-radius: 4px;
  display: none;
  overflow: scroll;
  line-height: 20px;
}

.modal.active {
  display: block;
}

.modal .cross {
  position: absolute;
  right: 1em;
  width: 1.1em;
  height: 1.1em;
  background: url(../img/svg/cross.svg) no-repeat;
  cursor: pointer;
  z-index: 3;
}

.modal .modal-content {
  padding: 1em;
}

.modal .modal-content h4 {
  font-weight: normal;
  font-family: 'helveticaneuecyrbold';
}

.modal .modal-content .col {
  width: 50%;
  float: left;
}

.modal-open .lightbox-background {
  display: block;
}

#modal-video {
  overflow: hidden;
  width: 800px;
  height: 450px;
}

#modal-video #youtube-video {
  width: 100%;
  height: 100%;
}
